* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  max-width: 960px;
  margin: 1rem auto;
}

h1 {
  text-align: center;
  margin-bottom: 1rem;
}

ul {
  list-style: none;
}

li {
  padding: 1rem 2rem;
  border: 1px solid #ccc;
  border-radius: 10px;
  margin-bottom: 1rem;
}
